header {
    height: 30vh;
    padding-top: 2rem;
    overflow: hidden;
}

.header__container {
    text-align: center;
    height: 100%;
    position: relative;
}

/* CTA */
.cta {
    margin-top: 1.5rem;
    display: flex;
    gap: 1.0rem;
    justify-content: center;
}

/* socials */
.header__socials {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    right: 0;
    top: 1rem;
}

.header__socials::after {
    content: '';
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

/* image */
.me {
    background: var(--color-primary);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 10rem 10rem 0 0;
    overflow: hidden;
    padding: 0 1.5rem 1.5rem 1.5rem;

}

/* scroll down */
.scroll__down {
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* tablets media query */
@media screen and (max-width: 1024px) {
    header {
        height: 30vh;
    }
}

/* phones media query */
@media screen and (max-width: 600px) {
    header {
            height: 35vh;
    }

    .header__socials, .scroll__down{
        display: none;
    }
}